body {
  margin: 0;
}
html,
body,
#root {
  height: 100%;
}

/*preloader innti*/

.overlay {
  opacity: 1;
  background-color: black;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  pointer-events: none;
  transition: opacity 1s ease;
}

.butoo {
  opacity: 1;
  position: fixed;
  width: 200px;
  height: 100px;
  border: none;
  color: white;
  background-color: transparent;
  font-size: 5rem;
  font-family: "Poor Story", monospace;
  transition: opacity 0.5s ease-in;
  bottom: 10px;
  right: calc((100vw - 200px) / 2);
}

p {
  margin: 0;
  position: fixed;
  width: 200px;
  height: 100px;
  border: none;
  color: white;
  background-color: transparent;
  font-size: 4rem;
  font-family: "Poor Story", monospace;
  transition: opacity 0.5s ease-in;
  top: calc((100vh - 80px) / 2);
  right: calc((100vw - 200px) / 2);
}

.butoo:hover {
  color: aqua;
}

.im1 {
  position: fixed;
  width: 100px;
  top: 50px;
  left: 150px;
  animation: rotatio infinite 3.3s;
}
.im2 {
  position: fixed;
  width: 100px;
  top: calc((100vh - 100px) / 2);
  right: calc((100vw) / 2 + 100px);
  animation: rotatio infinite linear 2.3s;
}
.im3 {
  position: fixed;
  width: 100px;
  top: 150px;
  right: 200px;
  animation: rotatio infinite 4.3s;
}
.im4 {
  position: fixed;
  width: 100px;
  bottom: 200px;
  left: 50px;
  animation: rotatio infinite 2.3s;
}
.im5 {
  position: fixed;
  width: 100px;
  bottom: 150px;
  right: 50px;
  animation: rotatio infinite 3s;
}
@keyframes rotatio {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.fade-in {
  opacity: 1;
}
.fade-out {
  opacity: 0 !important;
}
.wrpp {
  position: fixed;
  top: 80px;
  display: flex;
}
.wrpp img {
  width: min(500px, calc(100vw / 3));
}
@media (max-width: 600px) {
  .butoo {
    bottom: 50px;
  }
  .wrpp {
    left: calc((100vw - 160px) / 2);
    flex-direction: column;
  }
  .wrpp img {
    width: 160px;
  }
  #for-potato-users {
    bottom: unset !important;
    right: unset !important;
    top: 10px;
    left: 10px;
  }
  .im4 {
    bottom: unset !important;
    top: 200px;
    right: 50px;
    left: unset !important;
  }
}
#for-phone-users {
  position: fixed;
  bottom: 5px;
  left: 20px;
  color: white;
  font-size: 1rem;
  font-family: "Poor Story";
}
#for-potato-users {
  position: fixed;
  bottom: 5px;
  right: 20px;
  color: white;
  font-size: 1rem;
  font-family: "Poor Story";
  pointer-events: all;
}
#for-potato-users:hover {
  color: aqua;
}
