.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: fixed;
  top: 10px;
  right: 20px;
}

.hamRotate.active {
  transform: rotate(45deg);
}
.hamRotate180.active {
  transform: rotate(180deg);
}
.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: rgba(255, 255, 255, 0.268);
  stroke-width: 5.5;
  stroke-linecap: round;
}

@media (hover: hover) {
  .ham8:hover .line {
    stroke: rgb(255, 255, 255);
  }
}

.ham8 .top {
  stroke-dasharray: 40 160;
}
.ham8 .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.ham8 .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}
.ham8.active .top {
  stroke-dashoffset: -64px;
}
.ham8.active .middle {
  transform: rotate(90deg);
}
.ham8.active .bottom {
  stroke-dashoffset: -64px;
}
.active > .line {
  stroke: rgb(255, 255, 255);
}

#loadn {
  position: fixed;
  width: 200px;
  height: 100px;
  border: none;
  color: white;
  background-color: transparent;
  font-size: 3rem;
  font-family: "Poor Story", monospace;
  transition: opacity 0.5s ease-in;
  top: calc((100vh - 50px) / 2);
  right: calc((100vw - 200px) / 2);
}

#dot1 {
  animation: blinko ease 1.2s infinite;
}
#dot2 {
  animation: blinko ease 1.2s 0.3s infinite;
}
#dot3 {
  animation: blinko ease 1.2s 0.6s infinite;
}

#patienc {
  color: white;
  position: fixed;
  bottom: 10px;
  left: 10px;
  font-family: "Poor Story", monospace;
  font-size: 2rem;
}

@keyframes blinko {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

#overlay2 {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 10000000;
}
